body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('https://storage.googleapis.com/is-enablement-branding/HashiCorpEA-background-vertical.svg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a:link {
  color: black;
}

a:visited {
  color: black;
}

th {
  text-align: center;
  padding: 10px;
  padding-bottom: 0px;
}

td {
  padding: 10px;
}

.mainLogo {
  width: 300px;
}

.adminContainer{
  margin: 10px;
}

.avatar{
  width: 35px;
  height: 35px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.navlink{
  color: white !important;
}

.adminButton {
  margin-left: 5px;
  margin-right: 5px;
  padding-left: 5px;
  padding-right: 5px;
}

.course-content{
  text-align: center;
  margin-top: 10px;;
}

.home-content{
  text-align: center;
  margin-top: 10px;;
}

.labs-content{
  text-align: center;
  margin-top: 10px;;
}

.profile{
  text-align: center;
  font-size: large;
  font-weight: bold;
}

.profImg {
  width: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.redeemButton{
  margin-top: 20px;
  margin-bottom: 10px;
  margin-left: 20px;
  color: white;
}

.redeemCode{
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 10px;
  position: relative;
  top: 7px;
  left: 0px;
}

.regName{
  position: relative;
  top: 0px;
  left: 40px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.regCompany{
  position: relative;
  top: 0px;
  left: 11px;
  margin-bottom: 5px;
}

.regEmail{
  position: relative;
  top: 0px;
  left: 43px;
}

.regButton{
  color: white !important;
  margin: 10px;
}
